import {connect} from "react-redux";
import CreativeComponent from "./CreativeComponent";
import React, {useEffect, useState} from "react";
import Uppy from "@uppy/core";
import {getCreativeComment, uploadDoc} from "../../../redux/reducers/profile-reducer";

const CreativeContainer = (props) => {
    useEffect(() => {
        props.getCreativeComment(props.userId)
    }, [])
    const [isLoad, setLoad] = useState(0);
    const uppy = new Uppy({
        autoProceed: false,
        restrictions: {
            maxNumberOfFiles: 1,
            allowedFileTypes: [`application/msword`, `application/vnd.openxmlformats-officedocument.wordprocessingml.document`]
        }
    });

    uppy.on('file-added', (file) => {
        setLoad(1);
        props.uploadDoc(file.data, setLoad);
    });
    return (
        <CreativeComponent creativeComment={props.creativeComment} isLoad={isLoad} uppy={uppy} isUpload={props.isUpload}/>
    );
}

const mapStateToProps = (state) => ({
    isUpload: state.profilePage.profile.isUpload,
    userId: state.authPage.userId,
    creativeComment: state.profilePage.creativeComment
});

export default connect(mapStateToProps, {uploadDoc, getCreativeComment})(CreativeContainer);